<script>
// Extensions
import View from "@/views/View";

// Mixins
import { VueperSlides, VueperSlide } from "vueperslides";
import "vueperslides/dist/vueperslides.css";
const { validationMixin } = require("vuelidate");
const { required, maxLength, email } = require("vuelidate/lib/validators");

export default {
  name: "Home",

  metaInfo: { title: "Home" },
  components: { VueperSlides, VueperSlide },
  mixins: [validationMixin],

  validations: {
    name: { required, maxLength: maxLength(15) },
    email: { required, email },
    message: { required, maxLength: maxLength(150) },
    select: { required },
    checkbox: {
      checked(val) {
        return val;
      },
    },
  },

  extends: View,

  computed: {
    currentThemePrimary: {
      get() {
        return this.$vuetify.theme.currentTheme.primary;
      },
      set(val) {
        const target = this.$vuetify.theme.isDark ? "dark" : "light";

        this.$vuetify.theme.themes[target].primary = val;
      },
    },
    checkboxErrors() {
      const errors = [];
      if (!this.$v.checkbox.$dirty) return errors;
      !this.$v.checkbox.checked && errors.push("You must agree to continue!");
      return errors;
    },
    selectErrors() {
      const errors = [];
      if (!this.$v.select.$dirty) return errors;
      !this.$v.select.required && errors.push("Item is required");
      return errors;
    },
    nameErrors() {
      const errors = [];
      if (!this.$v.name.$dirty) return errors;
      !this.$v.name.maxLength &&
        errors.push("Message must be at most 10 characters long");
      !this.$v.name.required && errors.push("Name is required.");
      return errors;
    },
    emailErrors() {
      const errors = [];
      if (!this.$v.email.$dirty) return errors;
      !this.$v.email.email && errors.push("Must be valid e-mail");
      !this.$v.email.required && errors.push("E-mail is required");
      return errors;
    },

    messageErrors() {
      const errors = [];
      if (!this.$v.message.$dirty) return errors;
      !this.$v.message.maxLength &&
        errors.push("Name must be at most 150 characters long");
      !this.$v.message.required && errors.push("Message is required.");
      return errors;
    },
    minHeight() {
      const height = this.$vuetify.breakpoint.mdAndUp ? "100vh" : "50vh";

      return `calc(${height} - ${this.$vuetify.application.top}px)`;
    },

    target() {
      const value = this[this.type];
      if (!isNaN(value)) return Number(value);
      else return value;
    },
    options() {
      return {
        duration: this.duration,
        offset: this.offset,
        easing: this.easing,
      };
    },
  },
  props: {
    id: {
      type: String,
      default: "home",
    },
  },
  methods: {
    submit() {
      if (!this.$v.$invalid) console.log("everything is good");
      this.$v.$touch();
    },
  },
  watch: {
    group() {
      this.drawer = false;
    },
  },
  data: () => ({
    drawer: false,
    group: null,
    name: "",
    email: "",
    select: null,
    message: "",
    drawer: null,
    items: ["Home", "Work", "Portfolio", "Contact"],
    tab: "Home",
    type: "selector",
    number: 9999,
    selector: "#first",
    duration: 300,
    offset: 0,
    easing: "easeInOutCubic",
    skills: [
      { message: "Spark" },
      { message: "Hadoop" },
      { message: "Node.js" },
      { message: "Java" },
      { message: "C++/C" },
      { message: "Python" },
      { message: "Express.js" },
      { message: "React" },
      { message: "Vue.js" },
      { message: "SQL" },
      { message: "NoSQL" },
      { message: "HTML" },
      { message: "JavaScript" },
      { message: "CSS" },
    ],
    cards: [
      {
        icon: "mdi-laptop",
        title: "Full Stack Web Developer",
        text: "I have developed and maintained several full-stack web applications, working with both the back-end and front-end functionality using languages such as .NET/C#, React, Vue.js, Apex, Express.js/Node.js, HTML, CSS, and JavaScript. I also have optimized and devised SEO strategies on web applications centered around specific keywords to create rich content.",
      },
      {
        icon: "mdi-puzzle-outline",
        title: "Salesforce Developer",
        text: "In my current position, I am collaborating with several departments at UNLV to improve workplace productivity, customer experience, and employee stratification. This involves implementing custom Objects, APEX Classes, APEX Triggers, and Visual Force pages in Salesforce.",
      },
      {
        icon: "mdi-school-outline",
        title: "Teaching Assistance",
        text: "Assisted 200+ students from Computer Science I to Operating Systems with parallel programming, xv6 (teaching operation system), problem-solving methods, algorithm development, and data structures in C and C++.",
      },
    ],
    isIntersecting: false,
    foothyPics: [
      "@/assets/foothy1.png",
      "@/assets/foothy2.png",
      "@/assets/foothy3.png",
      "@/assets/foothy4.png",
      "@/assets/foothy5.png",
    ],
  }),
};
</script>

